@import url("https://fonts.googleapis.com/css2?family=Pacifico&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Roboto&display=swap");

.loginForm {
  align-self: center;
  justify-self: center;
  display: grid;
  width: 100%;
  height: 100%;
  grid-template-rows: 100px 100px auto;
  grid-template-areas:
    "title"
    "block"
    "bottom";
  grid-row-gap: 40px;
}
.header {
  grid-area: title;
  display: grid;
  width: 100%;
  height: 100%;
  border-bottom: 1px solid var(--bbLogin);
}

.title {
  justify-self: center;
  align-self: center;
  font-size: 30px;
  color: var(--fontColor);
  font-family: "Pacifico", cursive;
}
.inputsBlock {
  grid-area: block;
  width: 80%;
  height: 100px;
  justify-self: center;
  display: grid;
  grid-row-gap: 10px;
  grid-template-rows: 45px 45px;
}
.inputField {
  outline: none;
  box-sizing: border-box;
  background-color: var(--bacInputLogin);
  border: 1px solid var(--bacInputLogin);
  border-radius: 5px;
  padding: 5px;
  grid-template-rows: 1fr 1fr;
  justify-self: center;
}

.emailField {
  display: grid;
  align-self: center;
}
.passwordField {
  display: grid;
  align-self: center;
}

.bottomBlock {
  display: grid;
  grid-template-rows: 30px 40px 30px 30px 30px;
  grid-template-areas: "loginBut" "or" "forgotYourPassword" "error" "register";
}

.loginBut {
  grid-area: loginBut;
  justify-self: center;
  width: 80%;
  cursor: pointer;
  background-color: rgb(241, 145, 145);
  border: 1px solid rgb(241, 145, 145);
}
.loginBut:hover {
  background-color: rgb(214, 97, 97);
  border: 1px solid rgb(214, 97, 97);
}
.or {
  grid-area: or;
  justify-self: center;
  position: relative;
  color: var(--fontColor);
  display: grid;
  width: 80%;
}
.or span {
  justify-self: center;
  align-self: center;
  font-size: 18px;
}
.or::before {
  content: "";
  display: block;
  width: 40%;
  height: 1px;
  background-color: var(--fontColor);
  position: absolute;
  left: 0;
  top: 50%;
}
.or::after {
  content: "";
  display: block;
  width: 40%;
  height: 1px;
  background-color: var(--fontColor);
  position: absolute;
  right: 0;
  top: 50%;
}
.forgotYourPassword {
  grid-area: forgotYourPassword;
  display: flex;
  justify-content: center;
  align-self: center;
}
.forgotYourPassword a {
  text-decoration: none;
  font-size: 14px;
  font-family: "Roboto", sans-serif;
  color: var(--fontColor);
}
.register {
  grid-area: register;
  align-self: center;
  justify-self: center;
  width: 90%;
  display: grid;
  grid-template-columns: 80% 20%;
}
.regText {
  color: var(--fontColor);
  font-weight: bold;
}
.register a {
  justify-self: end;
  color: rgb(84 94 230);
  font-weight: bold;
}
.formSummaryError {
  box-sizing: border-box;
  justify-self: center;
  align-self: center;
  color: rgb(211, 33, 20);
}
